import request from '@/utils/request'

// 获取角色
export const geteRoles = (data) => request({ method: 'POST', url: '/boss/role/getRolePages', data })

// 删除角色
export const deleteRole = (id) => request({ method: 'DELETE', url: `/boss/role/${id}` })

// 保存或更新角色
export const addOrUpdateRole = (data) => request({ method: 'POST', url: '/boss/role/saveOrUpdate', data })

// 获取所有角色
export const getAllRoles = () => request({ method: 'GET', url: '/boss/role/all' })

// 给用户分配角色
export const allocateUserRoles = (data) => request({ method: 'POST', url: '/boss/role/allocateUserRoles', data })

// 查询用户角色
export const getUserRoles = (userId) => request({ method: 'GET', url: `/boss/role/user/${userId}` })
